
import { defineComponent } from 'vue'

export default defineComponent({
  data() {
    return {
      scrollPosY: false,
      menuOpen: false,
      disabled: false
    }
  },

  mounted() {
    this.updateScroll()
    window.addEventListener('scroll', this.updateScroll)
  },

  beforeUnmount() {
    window.removeEventListener('scroll', this.updateScroll)
  },

  methods: {
    updateScroll() {
      this.scrollPosY = window.scrollY > 0
    }
  }

})
